//import React, {useCallback} from "react";
import React from "react";
import { Link } from "react-router-dom";

export const SidebarIcon = ({ imageSrc, text, linkRef, newTab, iconClass }) => {
    let content = null;
    if(imageSrc!=null){
        content = <img alt="" src={imageSrc} />;
    }
    else{
        content = <div className="sidebar-icon-text">{text}</div>;
    }
    let target = null;
    if (newTab === "true") {
        target = <a href={linkRef} target="blank">{content}</a>;
    }
    else {
        target = <Link to={linkRef}>{content}</Link>;
    }

    return (
        <div className={"sidebar-icon "+iconClass}>{target}</div>
    );
}

